:root {
  --vermelho: #E50914;
  --preto: #141414;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--preto);
  font-family: 'Times New Roman', Times, serif;
  color: white;
}

/* header e navbar */

header #logo {
  color: var(--vermelho);
  font-size: 40px;
  margin-left: 40px;
}

.header-nav-link {
  text-decoration: none;
  color: #AAA;
  margin-right: 20px;
}

header nav {
  margin-right: 60px;
}

.header-nav-link:hover {
  color: #FFFF;
}

#container-header {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#container-header nav ul {
  display: flex;
  list-style: none;
  margin-right: 15px;
}

.active {
  color: white
}

/* filme principal */

.main-movie {
  font-size: 16px;
  height: 600px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
}

/* Capas da main-movie */

.main-movie-film {
  background: linear-gradient(rgba(0,0,0,.50), rgba(0,0,0,.50)100%), url(/src/img/capamovie.jpg);
  background-size: cover;
}

.main-movie-serie {
  background: linear-gradient(rgba(0,0,0,.50), rgba(0,0,0,.50)100%), url(/src/img/capaserie.jpg);
  background-size: cover;
}

.main-movie-home {
  background: linear-gradient(rgba(0,0,0,.50), rgba(0,0,0,.50)100%), url(/src/img/capa.jpg);
  background-size: cover;
  
}

.buttons {
  margin-top: 50px;
  display: flex;
}

.buttons a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  font-size: 40px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  margin-top: 5%;
}

.sinopse {
  margin-top: 15px;
}

.button {
  background-color:  rgba(255,255,255,.30);
  border: none;
  padding: 13px;
  margin-right: 7px;
  cursor: pointer;
  transition: .3s ease all;
  border-radius: 3px;
  text-decoration: none;
  color: white;
  font-weight: bold;
}

.button:hover {
  background-color: rgba(255,255,255,.20);
}

.container-main-movie {
  margin-left: 30px;
  width: 50%;
}

.button-1 {
  background-color: white;
  color: #141414;
}

.button-1:hover {
  background-color: rgba(255,255,255,.70);
}

.icons {
  margin-right: 5px;
}

/* carrossel */

.container {
  max-width: 100%;
}

.container .carousel {
  display: flex;
  overflow-x: auto;
  scroll-behavior: smooth;
  margin: 10px;
  padding: 10px;
}

.container .carousel::-webkit-scrollbar {
  display: none;
}

.container .carousel .item {
  width: 280px;
  display: flex;
  flex-wrap: wrap;
  transform: scale(0.95);
}

.container .carousel .item:hover {
  transform: scale(1);
}

.container .carousel .item .image img {
  width: 200px;
  height: 300px;
  object-fit: cover;
}

.container .title{
  display: flex;
  flex-direction: column;
  margin-top: -90px;
  margin-left: 30px;
  margin-bottom: -10px;
  font-size: 18px;
}

.container .buttons {
  display: flex;
  justify-content: center;
  margin-top: 5px;
  width: 100%;
  margin-bottom: 10px;
}

.container .buttons button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  width: 30px;
  color: white;
  font-weight: bold
}


/* cardDeitails */

.container-cardDetails {
  width: 100%;
}

.cardDeitails {
  /* background-size: cover; */
  height: 560px;
  width: 100%;
}

.container-card-details {
  max-width: 100%;
  display: flex;
  flex-direction: column;
}

.title-card-details {
  font-size: 40px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  margin-top: 5%;
  text-align: center;
}

.sinopse-card-details {
  margin-top: 5%;
  width: 40%;
  margin-left: 6.5%;
  margin-right: 0%;
}

.buttons-card-details {
  margin-top: -10%;
  display: flex;
  margin-left: 37.5%;
}

.buttons-card-details a {
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-details-image {
  margin-top: 5%;
  display: flex;
  flex-wrap: wrap;
  margin-left: 10%;
}

.card-details-image img {
  width: 200px;
  height: 300px;
  object-fit: cover;
  border-radius: 5px;
}

.container-sinopse-and-image {
  display: flex;
  width: 100%;
}



/* series */

.container-2 .title {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  margin-left: 30px;
  margin-bottom: -10px;
  font-size: 18px;
}

.main-serie {
  background: linear-gradient(rgba(0,0,0,.50), rgba(0,0,0,.50)100%), url(/src/img/capaserie.jpg);
  height: 800px;
  width: 100%;
}


.container-2 .title-2{
  margin-top: -100px;
}

/* filmes */

.main-movie-2 {
  background: linear-gradient(rgba(0,0,0,.50), rgba(0,0,0,.50)100%), url(/src/img/capamovie.jpg);
  width: 100%;
}

/* footer */

footer .text-footer {
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #AAA;
}
